import { Component,ElementRef,ViewChild  } from '@angular/core';
import { Sort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import {
  DataService,
  pageSelection,
  apiResultFormat,
  SidebarService,
} from 'src/app/core/core.index';
import { routes } from 'src/app/core/helpers/routes';
import { adminUsers } from 'src/app/shared/model/page.model';
import { PaginationService, tablePageSize } from 'src/app/shared/shared.index';
import Swal from 'sweetalert2';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
declare var bootstrap: any;
@Component({
  selector: 'app-admin-users',
  templateUrl: './adminUsers.component.html',
  styleUrl: './adminUsers.component.scss',
})
export class AdminUsersComponent {
  @ViewChild('addUserModal') addUserModal!: ElementRef;

  initChecked = false;
  selectedValue1 = '';
  selectedValue2 = '';
  selectedValue3 = '';
  selectedValue4 = '';
  selectedValue5 = '';
  selectedValue6 = '';
  selectedValue7 = '';

  public routes = routes;
  // pagination variables
  public tableData: Array<adminUsers> = [];
  public pageSize = 10;
  public serialNumberArray: Array<number> = [];
  public totalData = 0;
  showFilter = false;
  dataSource!: MatTableDataSource<adminUsers>;
  public searchDataValue = '';
  userForm!: FormGroup;
  profileImageBase64: string = ''; // Store the Base64 image

  constructor(
    private data: DataService,
    private pagination: PaginationService,
    private router: Router,
    private sidebar: SidebarService,
    private fb: FormBuilder
  ) {
    this.userForm = this.fb.group({
      first_name: ['', Validators.required],
      last_name: ['', Validators.required],
      phone_number: ['', [Validators.required, Validators.pattern('^[0-9]+$')]],
      country_code: ['', Validators.required],
      profile_img: [''], // Store Base64 image
    });

    this.data.getDataTable().subscribe((apiRes: apiResultFormat) => {
      this.totalData = apiRes.totalData;
      this.pagination.tablePageSize.subscribe((res: tablePageSize) => {
        if (this.router.url == this.routes.adminUsers) {
          this.getTableData({ skip: res.skip, limit: this.totalData });
          this.pageSize = res.pageSize;
        }
      });
    });
  }
  public onFileSelected(event: any): void {
    const file = event.target.files[0]; // Get the selected file
    if (file) {
      const reader = new FileReader();
      reader.readAsDataURL(file); // Read file as DataURL (Base64)
      reader.onload = () => {
        this.profileImageBase64 = reader.result as string; // Store Base64 string
        this.userForm.patchValue({ profile_img: this.profileImageBase64 }); // Update form value
      };
      reader.onerror = (error) => {
        console.error('Error converting image to Base64:', error);
      };
    }
  }


  // public onEditUser(user: users): void {
  //   this.editingUserId = user.id;
  //   this.profileImageBase64 = user.profile_img || '';

  //   // Patch user data into form
  //   this.userForm.patchValue({
  //     first_name: user.first_name,
  //     last_name: user.last_name,
  //     phone_number: user.phone_number,
  //     country_code: user.country_code,
  //     profile_img: user.profile_img
  //   });

  //   // Open Bootstrap modal
  //   const modalElement = document.getElementById('edit-units');
  //   if (modalElement) {
  //     const modal = new bootstrap.Modal(modalElement);
  //     modal.show();
  //   }
  // }


  public onSubmit(): void {
    if (this.userForm.valid) {
      this.data.createUser(this.userForm.value).subscribe(
        (response) => {
          console.log('User created successfully', response);
          this.getTableData({ skip: 0, limit: this.totalData });
          // Reset form after submission
          this.userForm.reset();
          this.profileImageBase64 = '';
           // Close the modal using Bootstrap's API
           const modalElement = document.getElementById('add-units');
           if (modalElement) {
             const modal = bootstrap.Modal.getInstance(modalElement);
             modal.hide();
           }
        },
        (error) => {
          console.error('Error creating user', error);
        }
      );
    }
  }
  public deleteUser(userId: string): void {
    Swal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#d33',
      cancelButtonColor: '#3085d6',
      confirmButtonText: 'Yes, delete it!'
    }).then((result) => {
      if (result.isConfirmed) {
        this.data.deleteUser(userId).subscribe(
          response => {
            Swal.fire('Deleted!', 'User has been deleted.', 'success');
            this.getTableData({ skip: 0, limit: this.totalData });
          },
          error => {
            Swal.fire('Error!', 'Failed to delete user.', 'error');
          }
        );
      }
    });
  }
  private getTableData(pageOption: pageSelection): void {
    this.data.getUsers().subscribe((apiRes: apiResultFormat) => {
      console.log(apiRes);
      this.tableData = [];
      this.serialNumberArray = [];
      this.totalData = apiRes.totalData;
      apiRes.data.map((res: adminUsers, index: number) => {
        const serialNumber = index + 1;
        if (index >= pageOption.skip && serialNumber <= pageOption.limit) {
          res.user_id = serialNumber;
          this.tableData.push(res);
          this.serialNumberArray.push(serialNumber);
        }
      });
      this.dataSource = new MatTableDataSource<adminUsers>(this.tableData);
      this.pagination.calculatePageSize.next({
        totalData: this.totalData,
        pageSize: this.pageSize,
        tableData: this.tableData,
        serialNumberArray: this.serialNumberArray,
      });
    });
  }

  public sortData(sort: Sort) {
    const data = this.tableData.slice();
    if (!sort.active || sort.direction === '') {
      this.tableData = data;
    } else {
      this.tableData = data.sort((a, b) => {
        const aValue = (a as never)[sort.active];
        const bValue = (b as never)[sort.active];
        return (aValue < bValue ? -1 : 1) * (sort.direction === 'asc' ? 1 : -1);
      });
    }
  }

  public searchData(value: string): void {
    this.dataSource.filter = value.trim().toLowerCase();
    this.tableData = this.dataSource.filteredData;
  }
  isCollapsed: boolean = false;
  toggleCollapse() {
    this.sidebar.toggleCollapse();
    this.isCollapsed = !this.isCollapsed;
  }
  public filter = false;
  openFilter() {
    this.filter = !this.filter;
  }
  confirmColor() {
    const swalWithBootstrapButtons = Swal.mixin({
      customClass: {
        confirmButton: ' btn btn-success',
        cancelButton: 'me-2 btn btn-danger',
      },
      buttonsStyling: false,
    });

    swalWithBootstrapButtons
      .fire({
        title: 'Are you sure?',
        text: "You won't be able to revert this!",
        confirmButtonText: 'Yes, delete it!',
        showCancelButton: true,
        cancelButtonText: 'Cancel',
        reverseButtons: true,
      })
      .then((result) => {
        if (result.isConfirmed) {
          swalWithBootstrapButtons.fire(
            'Deleted!',
            'Your file has been deleted.',
            'success'
          );
        } else if (result.dismiss === Swal.DismissReason.cancel) {
          swalWithBootstrapButtons.fire(
            'Cancelled',
            'Your imaginary file is safe :)',
            'error'
          );
        }
      });
  }

  public password: boolean[] = [false];

  public togglePassword(index: number) {
    this.password[index] = !this.password[index];
  }
  selectAll(initChecked: boolean) {
    if (!initChecked) {
      this.tableData.forEach((f) => {
        f.isSelected = true;
      });
    } else {
      this.tableData.forEach((f) => {
        f.isSelected = false;
      });
    }
  }
}
