import { Component, ElementRef, ViewChild } from '@angular/core';
import { Sort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import {
  DataService,
  pageSelection,
  apiResultFormat,
  SidebarService,
} from 'src/app/core/core.index';
import { routes } from 'src/app/core/helpers/routes';
import { users } from 'src/app/shared/model/page.model';
import { PaginationService, tablePageSize } from 'src/app/shared/shared.index';
import Swal from 'sweetalert2';
import {
  FormBuilder,
  FormGroup,
  Validators,
  FormControl,
} from '@angular/forms';
import { dA } from '@fullcalendar/core/internal-common';
declare var bootstrap: any;

interface Role {
  role_id: number;
  role_name: string;
}

@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrl: './users.component.scss',
})
export class UsersComponent {
  @ViewChild('addUserModal') addUserModal!: ElementRef;

  initChecked = false;
  selectedValue1 = '';
  selectedValue2 = '';
  selectedValue3 = '';
  selectedValue4 = '';
  selectedValue5 = '';
  selectedValue6 = '';
  selectedValue7 = '';

  public routes = routes;
  // pagination variables
  public tableData: Array<users> = [];
  public pageSize = 10;
  public serialNumberArray: Array<number> = [];
  public totalData = 0;
  showFilter = false;
  dataSource!: MatTableDataSource<users>;
  public searchDataValue = '';
  userForm!: FormGroup;
  userEditForm!: FormGroup;
  profileImageBase64: string = ''; // Store the Base64 image
  filterSelectedRoleId: number = 0;
  selectedRole: string = 'User';
  selectedRoleId: number = 0;
  userRoles: Role[] = [];
  editUserId!: number;
  allowedRoles: { role_id: number; role_name: string }[] = [
    { role_id: 5, role_name: 'Platform Admin' },
    { role_id: 6, role_name: 'Warehouse Admin' },
    { role_id: 7, role_name: 'Support Staff' },
  ];
  constructor(
    private data: DataService,
    private pagination: PaginationService,
    private router: Router,
    private sidebar: SidebarService,
    private fb: FormBuilder
  ) {
    this.userForm = this.fb.group({
      first_name: ['', Validators.required],
      last_name: ['', Validators.required],
      email: ['', [Validators.required, Validators.email]],
      password: ['', [Validators.required]],
      is_active: [true],
      user_role_id: [6],
      profile_img: [''],
    });
    this.userEditForm = this.fb.group({
      is_active: [true],
    });

    this.getTableData();

    this.data.getUserRoles().subscribe((apiRes: apiResultFormat) => {
      console.log(apiRes)
      this.userRoles = apiRes.data;
    });
  }
  onRoleSelect(role: string): void {
    this.userForm.reset();
    this.selectedRole = role;
    this.selectedRoleId = this.getRoleId(role);
  }

  getRoleId(roleName: string): number {
    const role = this.userRoles.find((r) => r.role_name === roleName);
    return role ? role.role_id : 0; // Returns 0 if role not found (avoid undefined)
  }
  public onFileSelected(event: any): void {
    const file = event.target.files[0]; // Get the selected file
    if (file) {
      const reader = new FileReader();
      reader.readAsDataURL(file); // Read file as DataURL (Base64)
      reader.onload = () => {
        this.profileImageBase64 = reader.result as string; // Store Base64 string
        this.userForm.patchValue({ profile_img: this.profileImageBase64 }); // Update form value
      };
      reader.onerror = (error) => {
        console.error('Error converting image to Base64:', error);
      };
    }
  }

  public onSubmit(): void {
    if (this.userForm.valid) {
      let updatedData = {
        ...this.userForm.value,
        role_name: this.selectedRole,
        user_role_id: this.selectedRoleId,
        admin_creation: true,
      };
      this.data.createUser(updatedData).subscribe(
        (response) => {
          this.getTableData();
          // Reset form after submission
          this.userForm.reset();
          this.profileImageBase64 = '';
          // Close the modal using Bootstrap's API
          const modalElement = document.getElementById('add-units');
          if (modalElement) {
            const modal = bootstrap.Modal.getInstance(modalElement);
            modal.hide();
          }
        },
        (error) => {
          console.error('Error creating user', error);
        }
      );
    }
  }

  public editUser(data: any): void {
    this.userEditForm = this.fb.group({
      is_active: [data.is_active ?? true], // Default to true if undefined
    });
    const rolesRequiringFullDetails = [
      'Platform Admin',
      'Warehouse Admin',
      'Support Staff',
    ];
    const userRole = data.user_role.role_name;
    this.selectedRole = data.user_role.role_name;
    this.selectedRoleId = this.getRoleId(data.user_role.role_name);
    if (rolesRequiringFullDetails.includes(userRole)) {
      this.userEditForm.addControl(
        'first_name',
        new FormControl(data.first_name || '', Validators.required)
      );
      this.userEditForm.addControl(
        'last_name',
        new FormControl(data.last_name || '', Validators.required)
      );
      this.userEditForm.addControl(
        'email',
        new FormControl(data.email || '', [
          Validators.required,
          Validators.email,
        ])
      );
      this.userEditForm.addControl(
        'user_role_id',
        new FormControl(this.selectedRoleId, Validators.required)
      );
    }
    
    this.editUserId = data.user_id;
  }

  public onUpdate(): void {
    if (this.userEditForm.valid) {
      let updatedData = this.userEditForm.value
      console.log(this.profileImageBase64)
      if(this.profileImageBase64!="")
        updatedData = {
        ...this.userEditForm.value,
        profile_img: this.profileImageBase64,
      };
      this.data.updateUser(this.editUserId, updatedData).subscribe(
        (response) => {
          this.getTableData();
          // Reset form after submission
          this.userForm.reset();
          this.profileImageBase64 = '';
          // Close the modal using Bootstrap's API
          const modalElement = document.getElementById('edit-units');
          if (modalElement) {
            const modal = bootstrap.Modal.getInstance(modalElement);
            modal.hide();
          }
        },
        (error) => {
          console.error('Error creating user', error);
        }
      );
    }
  }
  private getTableData(roleId?:any): void {
    this.data.getUsers(roleId).subscribe((apiRes: apiResultFormat) => {
      this.tableData = [];
      apiRes.data.forEach((res: users, index: number) => {
        // Push data to the table array
        this.tableData.push({
          ...res,
          user_role: res.user_role || { role_id: 0, role_name: 'Unknown Role' }, // Handle missing role
        });
      });
    });
  }
  public filterUsers() : void{
    if (this.filterSelectedRoleId === 0) {
      this.getTableData();
    } else {
      this.getTableData(this.filterSelectedRoleId);
    }
  }


  isCollapsed: boolean = false;
  toggleCollapse() {
    this.sidebar.toggleCollapse();
    this.isCollapsed = !this.isCollapsed;
  }
  public filter = false;
  openFilter() {
    this.filter = !this.filter;
  }
  confirmColor() {
    const swalWithBootstrapButtons = Swal.mixin({
      customClass: {
        confirmButton: ' btn btn-success',
        cancelButton: 'me-2 btn btn-danger',
      },
      buttonsStyling: false,
    });

    swalWithBootstrapButtons
      .fire({
        title: 'Are you sure?',
        text: "You won't be able to revert this!",
        confirmButtonText: 'Yes, delete it!',
        showCancelButton: true,
        cancelButtonText: 'Cancel',
        reverseButtons: true,
      })
      .then((result) => {
        if (result.isConfirmed) {
          swalWithBootstrapButtons.fire(
            'Deleted!',
            'Your file has been deleted.',
            'success'
          );
        } else if (result.dismiss === Swal.DismissReason.cancel) {
          swalWithBootstrapButtons.fire(
            'Cancelled',
            'Your imaginary file is safe :)',
            'error'
          );
        }
      });
  }

  public password: boolean[] = [false];

  public togglePassword(index: number) {
    this.password[index] = !this.password[index];
  }
}
